<template>
  <div class="popup__wrap" :class="{ on: popupEventFlag === 'Open' }">
    <div class="popup__bg"></div>
    <div class="common__popup box__shadow__02">
      <div class="popup__top">
        <h4>코드 {{ popupState }}</h4>
        <button class="pop__close__btn" @click="popupCloseClick"></button>
      </div>
      <ul>
        <li>
          <dl>
            <dt class="f__medium">분류코드명</dt>
            <dd>
              <select
                name=""
                id=""
                ref="mainCodeSelector"
                v-model="reqComCode.mainCommCode"
              >
                <option value="">선택</option>
                <option
                  v-for="code in mainCodeList"
                  :key="code.index"
                  :value="code.codeId"
                >
                  {{ code.codeName }}
                </option>
              </select>
            </dd>
          </dl>
        </li>
        <li>
          <dl>
            <dt class="f__medium">코드ID</dt>
            <dd>
              <input
                type="text"
                class="custom__input"
                v-model.trim="reqComCode.codeId"
                :disabled="popupState === '수정'"
              />
            </dd>
          </dl>
        </li>
        <li>
          <dl>
            <dt class="f__medium">코드명</dt>
            <dd>
              <input
                type="text"
                class="custom__input"
                v-model.trim="reqComCode.codeName"
              />
            </dd>
          </dl>
        </li>
        <li class="question__input">
          <dl>
            <dt class="f__medium">설명</dt>
            <dd>
              <textarea
                name=""
                id=""
                class="custom__input"
                v-model.trim="reqComCode.codeDesc"
              ></textarea>
            </dd>
          </dl>
        </li>
        <li>
          <dl>
            <dt class="f__medium">사용여부</dt>
            <dd>
              <select
                name=""
                id=""
                ref="useYnSelector"
                v-model="reqComCode.useYn"
              >
                <option value="">선택</option>
                <option value="Y">사용</option>
                <option value="N">미사용</option>
              </select>
            </dd>
          </dl>
        </li>
      </ul>
      <div class="custom__btn__wrap">
        <button class="custom__btn delete" @click="removeOrClean">
          <span>
            <img src="@/assets/images/icon/ic-remove.svg" alt="저장 아이콘" />
          </span>
          <p>삭제</p>
        </button>
        <button class="custom__btn" @click="addCommCode">
          <span>
            <img src="@/assets/images/icon/ic-save.svg" alt="저장 아이콘" />
          </span>
          <p>저장</p>
        </button>
      </div>
    </div>
  </div>
</template>
<script>
import { addCommCode, updateCommCode, removeCommCode } from "@/api/code";
import { clear } from "@/utill/commom";
export default {
  props: {
    popupEventFlag: String,
    mainCodeList: Array,
    popupState: String,
    selectedObj: [Object, Boolean],
  },
  data() {
    return {
      reqComCode: {
        mainCommCode: "",
        codeId: "",
        codeName: "",
        codeDesc: "",
        useYn: "",
      },
    };
  },
  watch: {
    popupState() {
      if (this.popupState === "수정") {
        this.reqComCode = this.selectedObj;
        if (this.selectedObj.useYn === "사용") {
          this.reqComCode.useYn = "Y";
        } else {
          this.reqComCode.useYn = "N";
        }
      } else if (this.popupState === "추가") {
        clear(this.reqComCode);
      }
    },
  },
  methods: {
    popupCloseClick() {
      this.$emit("popupClose", "Close");
    },
    addCommCode() {
      if (this.validateComCode()) {
        if (this.$env == "development") console.log(this.reqComCode);
        if (this.popupState === "수정") {
          updateCommCode(this.reqComCode)
            .then((response) => {
              if (response.data.status === "OK") {
                this.$toast.success("공통코드 수정이 완료되었습니다");
                this.popupCloseClick();
                this.$emit("refreshList");
              }
            })
            .catch((error) => this.$toast.error(error.message));
        } else {
          addCommCode(this.reqComCode)
            .then((response) => {
              if (this.$env == "development") console.log(response);
              if (response.data.status === "OK") {
                this.$toast.success("공통코드 추가가 완료되었습니다");
                this.popupCloseClick();
                this.$emit("refreshList");
              }
            })
            .catch((error) => this.$toast.error(error.message));
        }
      }
    },
    validateComCode() {
      if (
        this.reqComCode.mainCommCode === "" ||
        this.reqComCode.mainCommCode === null
      ) {
        this.$toast.error("분류코드명을 선택하세요.");
        return false;
      }
      if (this.reqComCode.codeId === "" || this.reqComCode.codeId === null) {
        this.$toast.error("코드 아이디를 입력하세요.");
        return false;
      }
      if (
        this.reqComCode.codeName === "" ||
        this.reqComCode.codeName === null
      ) {
        this.$toast.error("코드 명을 입력하세요.");
        return false;
      }
      // if (
      //   this.reqComCode.codeDesc === "" ||
      //   this.reqComCode.codeDesc === null
      // ) {
      //   this.$toast.error("코드 설명을 입력하세요.");
      //   return false;
      // }
      if (this.reqComCode.useYn === "" || this.reqComCode.useYn === null) {
        this.$toast.error("사용여부를 입력하세요.");
        return false;
      }
      return true;
    },
    removeOrClean() {
      if (this.popupState === "수정") {
        this.$confirm({
          // showCancelButton: true,
          title: "",
          message: "정말로 삭제 하시겠습니까?",
          button: {
            no: "아니오",
            yes: "네",
          },
          callback: (confirm) => {
            if (confirm) {
              removeCommCode(this.reqComCode.codeId)
                .then((response) => {
                  if (this.$env == "development") console.log(response);
                  if (response.data.status === "OK") {
                    this.$toast.success("공통코드가 삭제되었습니다");
                    this.popupCloseClick();
                    this.$emit("refreshList");
                  }
                })
                .catch((error) => this.$toast.error(error.message));
            }
          },
        });
      } else if (this.popupState === "추가") {
        clear(this.reqComCode);
      }
    },
  },
};
</script>
