<template>
  <main class="commoncode__management__main">
    <CommonCodePopup
      :mainCodeList="mainCodeList"
      :popupEventFlag="popupEventFlag"
      @popupClose="popupToggle"
      :popupState="popupState"
      :selectedObj="selectedRowObj"
      @refreshList="getCommonCodes"
    />
    <section class="box__shadow__01 vertical__scroll grid__wrap">
      <h3 class="page__title">
        {{ this.$store.state.pageInfo["공통코드 관리"].title }}
      </h3>
      <h5 class="page__sub__title f__regular">
        {{ this.$store.state.pageInfo["공통코드 관리"].subTitle }}
      </h5>
      <div class="add__or__search__wrap">
        <ul>
          <li>
            <dl>
              <dt>분류코드</dt>
              <dd>
                <select
                  name=""
                  id=""
                  ref="mainCodeSelector"
                  v-model="mainCodeFilter"
                  @change="gridDataFiltering"
                >
                  <option value="">전체</option>
                  <option
                    v-for="code in mainCodeList"
                    :key="code.index"
                    :value="code.codeId"
                  >
                    {{ code.codeName }}
                  </option>
                </select>
              </dd>
            </dl>
          </li>
          <li>
            <dl>
              <dt>사용여부</dt>
              <dd>
                <select
                  name=""
                  id=""
                  ref="useYnSelector"
                  v-model="useYnFilter"
                  @change="gridDataFiltering"
                >
                  <option value="">전체</option>
                  <option value="사용">사용</option>
                  <option value="미사용">미사용</option>
                </select>
              </dd>
            </dl>
          </li>
        </ul>
        <button class="custom__btn" @click="popupToggle('추가')">
          <span>
            <img src="@/assets/images/icon/ic-plus.svg" alt="추가 아이콘" />
          </span>
          <p>코드 추가</p>
        </button>
      </div>
      <div class="grid__wrap" @click="rowClick($event)">
        <Grid
          class="on"
          ref="gridEl"
          :cols="cols"
          :pagination="true"
          :sort="true"
          :rows="filteredGridData"
        ></Grid>
      </div>
    </section>
  </main>
</template>
<script>
import "@/assets/css/commonCodeManagement.css";
import { Grid } from "gridjs-vue";
import CommonCodePopup from "@/components/SystemManagement/CommonCodeManagement/CommonCodePopup";
import { getCommonCodeList, getMainCodeList } from "@/api/code";

export default {
  data() {
    return {
      popupEventFlag: "Close",
      popupState: "",
      selectedRowObj: "",
      cols: [
        { name: "번호", id: "index" },
        { name: "분류코드", id: "mainCommCode" },
        { name: "코드명", id: "codeName" },
        { name: "코드ID", id: "codeId" },
        { name: "상태", id: "useYn" },
        { name: "등록자명", id: "registerName" },
        { name: "변경일시", id: "modifyDate" },
      ],
      commonCodeList: [],
      mainCodeList: [],
      mainCodeFilter: "",
      useYnFilter: "사용",
      filteredGridDataBack: [],
      filteredGridData: [],
    };
  },
  components: {
    Grid,
    CommonCodePopup,
  },
  computed: {
    getCommonCodeList() {
      return this.commonCodeList;
    },
  },
  created() {
    this.getCommonCodes();
    this.getMainCodes();
  },
  methods: {
    popupToggle(evtName) {
      if (evtName === "추가") this.selectedRowObj = false;
      this.popupState = evtName;
      this.popupEventFlag === "Close"
        ? (this.popupEventFlag = "Open")
        : (this.popupEventFlag = "Close");
    },
    rowClick(param) {
      const selectedObjIndex = Number(param.path[1].childNodes[0].textContent);
      const selectedCell = param.path[0].getAttribute("data-column-id");
      if (selectedCell === "codeId") this.popupToggle("수정");
      this.selectedRowObj = { ...this.getCommonCodeList[selectedObjIndex - 1] };
    },
    getCommonCodes() {
      getCommonCodeList().then((response) => {
        this.commonCodeList = response.data.response.codeList;
        this.commonCodeList.forEach(function (object, index) {
          object.index = index + 1;
          if (object.useYn === "Y") {
            object.useYn = "사용";
          } else {
            object.useYn = "미사용";
          }
        });
        this.filteredGridDataBack = this.commonCodeList;
        if (this.mainCodeFilter === "" && this.useYnFilter === "사용") {
          this.filteredGridData = this.commonCodeList;
          this.filteredGridData = this.filteredGridData.filter(
            (obj) => obj.useYn.trim() === "사용"
          );
        } else {
          this.gridDataFiltering();
        }
      });
    },
    getMainCodes() {
      getMainCodeList().then((response) => {
        this.mainCodeList = response.data.response.classificationCodeList;
      });
    },
    searchCommCode() {
      let searchQuery = "";
      if (this.mainCodeFilter !== "") {
        searchQuery += "mainCommCode=" + this.mainCodeFilter;
      }
      if (this.useYnFilter !== "") {
        searchQuery += "&useYn=" + this.useYnFilter;
      }
      getCommonCodeList(searchQuery).then((response) => {
        this.commonCodeList = response.data.response.codeList;
        this.commonCodeList.forEach(function (object, index) {
          object.index = index + 1;
          if (object.useYn === "Y") {
            object.useYn = "사용";
          } else {
            object.useYn = "미사용";
          }
        });
      });
    },
    gridDataFiltering() {
      this.filteredGridData = this.filteredGridDataBack;

      if (this.mainCodeFilter !== "") {
        this.filteredGridData = this.filteredGridData.filter(
          (obj) => obj.mainCommCode.trim() === this.mainCodeFilter
        );
      }
      if (this.useYnFilter !== "") {
        this.filteredGridData = this.filteredGridData.filter(
          (obj) => obj.useYn.trim() === this.useYnFilter
        );
      }
    },
  },
};
</script>
